import { useConfig } from '@dhis2/app-runtime';

const useHrefs = ({
  endpoint,
  fileTypes,
  queryStr
}) => {
  const {
    baseUrl,
    apiVersion
  } = useConfig();
  const timestamp = Date.now(); // The new outlierDetection endpoint produces the correct report
  // based on query params, but older endpoints only take a timestamp

  queryStr = queryStr || "t=".concat(timestamp);
  return fileTypes.reduce((acc, type) => {
    const downloadUrl = "".concat(baseUrl, "/api/").concat(apiVersion, "/").concat(endpoint, ".").concat(type, "?").concat(queryStr);
    acc[type] = downloadUrl;
    return acc;
  }, {});
};

export default useHrefs;